'use strict';
var _self;
import { getMyShop} from '@/api/measureHome.js';
import NullData from '@/components/NullData.vue';
export default {
	components:{
		NullData
	},
	data() {
		return{
			dataList:{}
		}
	},
	methods:{
		
	},
	async onLoad(options) {
		_self=this
		let e = await getMyShop()
		_self.dataList=e.list
		
	}
};
